import VueRouter from 'vue-router'
import start from './components/start'
// import level1 from './components/level1'
// import level2 from './components/level2'
// import level3 from './components/level3'
// import level4 from './components/level4'
// import level5 from './components/level5'
// import level6 from './components/level6'
// import level7 from './components/level7'
const level1 = resolve => {
   require.ensure(['./components/level1.vue'],() => {
      resolve(
         require('./components/level1.vue')
      )
   })
}
const level2 = resolve => {
   require.ensure(['./components/level2.vue'], () => {
      resolve(
         require('./components/level2.vue')
      )
   })
}
const level3 = resolve => {
   require.ensure(['./components/level3.vue'], () => {
      resolve(
         require('./components/level3.vue')
      )
   })
}
const level4 = resolve => {
   require.ensure(['./components/level4.vue'], () => {
      resolve(
         require('./components/level4.vue')
      )
   })
}
const level5 = resolve => {
   require.ensure(['./components/level5.vue'], () => {
      resolve(
         require('./components/level5.vue')
      )
   })
}
const level6 = resolve => {
   require.ensure(['./components/level6.vue'], () => {
      resolve(
         require('./components/level6.vue')
      )
   })
}
const level7 = resolve => {
   require.ensure(['./components/level7.vue'], () => {
      resolve(
         require('./components/level7.vue')
      )
   })
}
export default new VueRouter({
   routes:[
      {
        path: '',
        component: start
      },
      {
         path: '/level1',
         component: level1
       },
      {
         path: '/level2',
         component: level2
      },
      {
         path: '/level3',
         component: level3
      },
      {
         path: '/level4',
         component: level4
      },
      {
         path: '/level5',
         component: level5
      },
      {
         path: '/level6',
         component: level6
      },
      {
         path: '/level7',
         component: level7
      }
   ],
   mode: 'history'
})

